import axios from "axios";
import React, { useRef, useState } from "react";
import { useHistory } from "react-router-dom";
import LoadingSpinnerWhite from "../loadingSpinner/loadingSpinnerWhite/loadingSpinnerWhite";
import styles from "./loginPage.module.css";

axios.defaults.withCredentials = true;

const LoginPage = () => {
  const history = useHistory();
  const idRef = useRef();
  const pwRef = useRef();
  const [loadingOn, setLoadingOn] = useState(false);

  const refresh = () => {
    window.location.href = "/";
    return;
  };

  const loginSubmitHandler = async (e) => {
    e.preventDefault();

    const id = idRef.current.value;
    const password = pwRef.current.value;

    if (id === "" || password === "") {
      window.alert("  ");
      return;
    }

    setLoadingOn(true);

    await axios
      .post(
        `${process.env.REACT_APP_BASEURL}/user/login`,
        {
          id,
          password,
        },
        { withCredentials: true }
      )
      .then((response) => {
        console.log(response);
        if (response.data === "loggedin") {
          console.log(response);
          window.alert(" .");
          refresh();
        } else {
          window.alert("   ");
        }
        setLoadingOn(false);
      })
      .catch((err) => {
        setLoadingOn(false);
        console.error(err);
      });
  };
  return (
    <section className={styles.login_page}>
      <section className={styles.container}>
        <p className={styles.title}></p>
        <form className={styles.main}>
          <div className={styles.input_container}>
            <input
              ref={idRef}
              type="text"
              className={styles.input}
              placeholder=""
              spellCheck="false"
            />
            <input
              ref={pwRef}
              type="password"
              className={styles.input}
              placeholder=""
              spellCheck="false"
            />
          </div>
          <button className={styles.submit_button} onClick={loginSubmitHandler}>
            {loadingOn ? <LoadingSpinnerWhite /> : ""}
          </button>
        </form>
        <div className={styles.id_save_find_container}>
          <div className={styles.id_save_container}>
            <input type="checkbox" className={styles.id_save_checkbox} />
            <span className={styles.id_save_text}> </span>
          </div>
          <div className={styles.id_pw_find_container}>
            <button
              className={`${styles.id_pw_find_button} ${styles.left}`}
              onClick={() => {
                history.push("/find");
              }}
            >
              
            </button>
            |
            <button
              className={styles.id_pw_find_button}
              onClick={() => {
                history.push("/find");
              }}
            >
              
            </button>
          </div>
        </div>
        <div className={styles.sns_login_container}>
           SNS 
        </div>
        <div className={styles.signup_container}>
          <span className={styles.signup_message}>
                  .
          </span>
          <button
            className={styles.go_signup_button}
            onClick={() => {
              history.push("/signup");
              window.scrollTo({ top: 0 });
            }}
          >
            
          </button>
        </div>
      </section>
    </section>
  );
};

export default LoginPage;
